import styled from 'styled-components';
import { NavLink } from 'react-router-dom';

const categories = [
  {
    name: 'all',
    text: '전체보기'
  },
  {
    name: 'business',
    text: '비즈니스'
  },
  {
    name: 'entertainment',
    text: '엔터테인먼트'
  },
  {
    name: 'health',
    text: '건강'
  },
  {
    name: 'science',
    text: '과학'
  },
  {
    name: 'sports',
    text: '스포츠'
  },
  {
    name: 'technology',
    text: '테크'
  }
];

const CategoriesBlock = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom: 4px black double;
  padding: 1rem;
  width: 100%;
  margin: 0 auto 1rem auto;

  @media screen and (max-width: 768px) {
    width: 100%;
    overflow-x: auto;
  }
`;

const Category = styled(NavLink)`
  font-size: 1.125rem;
  cursor: pointer;
  white-space: pre;
  text-decoration: none;
  color: inherit;
  padding-bottom: 0.25rem;

  &:hover {
    color: #495057;
  }

  &.active {
    font-weight: 600;
    border-bottom: 2px solid black;
    color: black;
    &:hover {
      color:color: #3bc9db;
    }
  }

  & + & {
    margin-left: 1rem;
  }

  @media screen and (max-width: 768px) {
    font-size: 12px;
    & + & {
      margin-left: 5px;
    }
  }
`;

const Categories = () => {
  return (
    <CategoriesBlock>
      {categories.map(c => (
        <Category 
        key={c.name}
        activeClassName="active"
        exact={c.name === 'all'}
        to={c.name === 'all' ? '/' : `${c.name}`}
        >
          {c.text}
        </Category>
      ))}
    </CategoriesBlock>
  );
};

export default Categories;
