import { useState, useEffect } from 'react';
import styled from 'styled-components';
import NewsItem from './NewsItem';
import axios from 'axios';

const NewsListBlock = styled.div`
  column-count: 2;
  column-gap: 1em;
  box-sizing: border-box;
  padding-bottom: 3rem;
  margin: 0 auto;
  margin-top: 2rem;

  .theEnd {
    border-top: 4px double black;
    text-align: center;
    font-family: 'IM Fell English', serif;
    font-style: italic;
    padding: 1rem 0;
  }

  @media screen and (max-width: 768px) {
    column-count: 1;
    width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
  }
`;


const NewsList = ({ category }) => {
  const [articles, setArticles] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const query = category === 'all' ? '' : `&category=${category}`;
        const response = await axios.get (
          `http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=fc9b588ff6524ed789b40247a05ff783`
        );
        setArticles(response.data.articles);
      } catch (e) {
        console.log(e);
      }
      setLoading(false);
    };
    fetchData();
  }, [category]);

  //대기 중일 때
  if (loading) {
    return <NewsListBlock>대기 중...</NewsListBlock>;
  }

  //아직 articles 값이 설정되지 않았을 때
  //이거 필수: 현재 값이 null인지 확인하지 않으면, 아직 데이터가 없을 때
  //null에는 map 함수가 없기 때문에 렌더링 과정에서 오류가 발생함. (흰페이지 나옴)
  if (!articles) {
    return null;
  }

  //articles 값이 유효할 때
  return (
    <NewsListBlock>
      {articles.map(article => (
        <NewsItem key={article.url} article={ article } />
      ))}
      <div className="theEnd">- The End -</div>
    </NewsListBlock>
  )
};

export default NewsList;
