import styled from 'styled-components';

const HeaderStyle = styled.div`
  width: 100%;
  font-family: 'IM Fell English', serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 4px black double;
  padding: 1rem 0;
  .title {
    margin: 0;
    font-size: 60px;
    font-style: italic;
    letter-spacing: 3px;
  }
  .title2 {
    margin: 0;
    font-size: 20px;
    letter-spacing: 3px;
  }
  .today {
    font-size: 20px;
  }
  @media screen and (max-width: 500px) {
    .title {
      font-size: 40px;
    }
    .title2 {
      font-size: 15px;
    }
    .today {
      font-size: 13px;
    }
  }

`;

const date = new Date();

const Header = () => {
  return (
    <HeaderStyle>
      <h1 className="title">Daily Paper</h1>
      <h3 className="title2">SOUTH KOREA</h3>
      <p className="today">{ `Date: ${date.getFullYear()}. ${date.getMonth()+1}. ${date.getDate()}` }</p>
    </HeaderStyle>
  )
}

export default Header
